$fonts-path: '../fonts/';

// Akrobat

@font-face {
	// Akrobat-Regular
	font-family: 'Akrobat';
	src:
	url($fonts-path + 'Akrobat-Regular.otf') format('opentype'),
	url($fonts-path + 'Akrobat-Regular.eot'),
	url($fonts-path + 'Akrobat-Regular.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

