.dd-bar {
	min-height: 80px;
	height: 80px;
	color: #aaaaaa;
    font-size: 12px;
    line-height: 1;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0 0 5px 0;
    font-family: 'Akrobat', Tahoma, Verdana, sans-serif;
    z-index: 2;
    margin: auto;

    &-container {
    	max-width: 2560px;
    	padding: 0 10px;
    	margin: 0 auto;

    	display: table;
    	vertical-align: middle;
    	width: 100%;
    	height: 100%;
    }

    &-toggle {
	    width: 40px;
	    height: 40px;
	    background-image: url("data:image/png;base64,");
	    background-repeat: no-repeat;
	    background-position: center;
	    cursor: pointer;
	    position: absolute;
	    top: 18px;
	    right: 0;
	    z-index: 2014;
	    cursor: pointer;
    }
}

.dd-nav {
    margin: auto;
    position: relative;
    font-size: 12px;
    border-bottom: 1px solid #8ea7ab;

    display: table-cell;
    vertical-align: middle;

    &::after {
    	display: table;
    	content: "";
    	clear: both;
    }

    &-logo {
    	position: relative;
	    float: left;
    }

    &-auth {
	    float: right;
	    font-size: 20px;
	    font-weight: normal;

	    a {

	    	&:hover {
	    		border-bottom: 1px dashed #fff;
	    		color: #fff;
    		}

    		&:active {
    			color: #8196a7;
    			border-bottom: 1px dashed #8196a7;
    		}
	    }
    }

    &-profile {
    	position: relative;
	    float: right;
	    font-size: 20px;
        margin-right: 160px;
    }

    &-stats {
    	font-size: 20px;
    	color: #8ea7ab;
    	float: right;
    	margin-right: 25px;

    	span {
    		color: #aaaaaa;
    	}
    }
}

.dd-login,
.dd-register,
.dd-profile-name {
    text-transform: uppercase;
}

.dd-menu-opener {
    user-select: none;

    &-link {
    	cursor: pointer;
		&:hover {
			color: #fff;
		}
    }
}

.dd-games {
    list-style-type: none;
    height: 30px;
    width: 222px;
    position: absolute;
    top: 0;
    left: 0;

    &-btn {
    	font-size: 20px;
		text-transform: uppercase;
	    float: right;
    	width: 65px;
    }
}

.dd-logo {
    height: 30px;
    position: absolute;
    top: 0;
	left: 0;
	width: auto;

	a {
		display: block;
		width: 115px;
		height: 25px;
		margin-right: 40px;
		background-image: url("data:image/png;base64,");
	}
}

