.section-1 {
	text-align: center;
	background-image: url("../img/sec-1_bg.jpg");
	background-size: 1680px;
	background-position: top center;
    height: 1210px;
    overflow: hidden;
    position: relative;

    &::after {
    	content: "";
    	display: block;
    	position: absolute;
    	bottom: -50px;
    	left: 0;
    	right: 0;
    	background-image: url("../img/fog.png");
    	background-repeat: no-repeat;
    	background-size: cover;
    	z-index: 201;
    	padding-bottom: 33%;
    }

	&_content {
		width: 100%;
		height: 100%;
	    max-width: 620px;
	    margin: 0 auto;
	    position: relative;
	}

	@media screen and (min-width: 1680px) {
		background-size: auto;
		height: 1844px;

		&_content { 
			max-width: 960px;
		}
	}

	@media screen and (min-width: 1920px) {

	}
}

.intro {
    padding-top: 80px;

    &_logo {
		margin: 74px 0 16px;

		img {
			width: 250px;
		}
    }

    &_text {
        width: 100%;
        max-width: 810px;
        margin: 40px auto 0;
        font-size: 16px;
    }

    &_title {
        margin: 0 0 26px;
        font-size: 35px;
    }

    @media screen and (min-width: 1680px) {
		&_logo {
			img {
				width: 400px;
			}
	    }

	    &_title {
	    	margin-bottom: 121px;
	    	font-size: 60px;
	    }

	    &_text {
	    	font-size: 22px;
	    }
    }
}

.hand {
	display: block;
    width: 570px;
    height: 880px;
	position: absolute;
	z-index: 200;
    left: -400px;
    bottom: -200px;
	background-image: url("../img/hand.png");
	background-repeat: no-repeat;
	background-size: contain;
	transition: .2s;

	&.hand-off {
		left: -520px !important;
		bottom: -313px !important;
		transition: .6s;
	}

	@media screen and (min-width: 1680px) {
		width: 854px;
		height: 1188px;
	    bottom: -150px;
    	left: -600px;

    	&.hand-off {
    		left: -784px !important;
    		bottom: -270px !important;
    	}
	}
}