@import '_mixins/all';
@import 'normalize';
@import 'variables';
@import 'layout';
@import 'icons';
@import 'fonts';

@import 'blocks/dd-bar';
@import 'blocks/page-footer';
@import 'blocks/page-nav';
@import 'blocks/section-1';
@import 'blocks/section-2';
@import 'blocks/section-3';

* {
    box-sizing: border-box;
}

html {
  height: 2560px;
}

body, html {
    height: 100%;
    min-width: 1024px;
    margin: 0 auto;
}

body {
    min-height: 100%;
    font-family: "ArnoPro", serif;
    position: relative;
    background: #333;
    color: #fff;
    font-size: 22px;
    line-height: 1.25;
    position: relative;
}

h2 {
    font-family: 'GoudyTrajan', 'Garamond', serif;
    text-transform: uppercase;
    font-size: 35px;

    @media screen and (min-width: 1680px) {
      font-size: 50px;
    }
}

a {
    cursor: pointer;
}

p {
  line-height: 1.36;
}

@media screen and (min-width: 1680px) {
  p {
    line-height: 1.4;
  }
}

.btn {
  display: block;
  vertical-align: middle;
  width: 420px;
  height: 70px;
  line-height: 72px;
  font-size: 22px;
  text-transform: uppercase;
  border: 0;
  padding: 0;
  white-space: nowrap;
  font-family: "GoudyTrajan", serif;
  text-decoration: none;
  font-weight: bold;
  text-align: center;
  color: #fff;
  cursor: pointer;
  position: relative;
  z-index: 300;
  margin: 0 auto;

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: -6px;
    right: -6px;
    top: -1px;
    bottom: 0px;
    background: rgba(255,255,255,.33);
    border-radius: 10px;
    z-index: -2;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: -1;
  }

  @media screen and (min-width: 1680px) {
    width: 627px;
    height: 104px;
    line-height: 108px;
    font-size: 32px;
    top: 0px;
  }
}

.btn-red {

  &::before {
    background-image: url("../img/button.gif");
    background-repeat: no-repeat;
    background-size: contain;
  }

  &:hover::before {
    background-image: url("../img/button_hover.gif");
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.content {
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
}
.trailer-btn {
  display: block;
  width: 100%;
  padding: 0;
  margin-bottom: 30px;
  background-color: transparent;
  position: relative;
  border: 0;
  cursor: pointer;

  &:focus {
    outline: none;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    background-image: url("../img/trailer-preview.png");
    background-size: cover;
    background-repeat: no-repeat;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 100;
    opacity: 1;
    visibility: visible;
    transition: opacity 0.5s 0s, visibility 0s 0.5s;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    z-index: 101;
    width: 78px;
    height: 87px;
    top: 50%;
    left: 50.8%;
    transform: translate(-50%, -50%);
    background-image: url("data:image/png;base64,");
    background-size: 78px;
  }

  &.playing::before,
  &.playing::after { 
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s 0s, visibility 0s 0.5s;
  }

  @media screen and (min-width: 1680px) {
    margin-bottom: 40px;
  }
}

.embed-responsive {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  overflow: hidden;
}

.embed-responsive::before {
  display: block;
  content: "";
}

.embed-responsive .embed-responsive-item,
.embed-responsive iframe,
.embed-responsive embed,
.embed-responsive object,
.embed-responsive video {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.embed-responsive-16by9::before {
  padding-top: 56.25%;
}

.embed-responsive-4by3::before {
  padding-top: 75%;
}

.text-box {
  max-width: 620px;
  color: #fff;
  font-size: 16px;
  padding: 20px 30px 0;
  background: rgba(0,0,0,.75);
  border-radius: 6px;

  p {
    margin: 10px 0;
  }

  &_title {
    font-size: 26px;
    text-align: center;
    margin: 0 0 ;
  }

  @media screen and (min-width: 1680px) {
    max-width: 960px;
    padding: 50px 70px 0;
    font-size: 22px;

    p {
      margin: 22px 0;
    }

    &_title {
      font-size: 36px;
      margin-bottom: 30px;
    }
  }
}

.crafts-list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-around;
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "GoudyTrajan", serif;
  font-weight: bold;
  font-size: 13px;
  text-align: center;
  white-space: nowrap;
  margin-top: 15px;

  li {
    max-width: 23%;
    flex-basis: 23%;
    flex-shrink: 0;
    flex-grow: 0;
    display: inline-block;
    vertical-align: bottom;
    margin: 0 10px;
    position: relative;
    padding-top: 88px;
    background-repeat: no-repeat !important;
    background-position: center top;
    background-size: 56px;
  }
  
  &_update {
    background-image: url("../img/craft/update.png");
  }

  &_upgrade {
    background-image: url("../img/craft/upgrade.png");
  }

  &_armor {
    background-image: url("../img/craft/armor.png");
  }

  &_craft {
    background-image: url("../img/craft/craft.png");
  }

  @media screen and (min-width: 1680px) {
    font-size: 20px;
    margin-top: 35px;

    li {
      padding-top: 145px;
      background-size: 90px;
    }
  }
}