.section-2 {
  text-align: center;
  position: relative;
  height: 1px;
  margin-top: -1px;
  background-color: #262526;

  img {
  	width: 500px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
  }

  @media screen and (min-width: 1280px) {
  	img {
  		margin-top: 50px;
  	}
  }

  @media screen and (min-width: 1680px) {
  	img {
  		width: 700px;
  		margin-top: 20px;
  	}
  }

  @media screen and (min-width: 1920px) {
  	img {
  		margin-top: 0;
		width: 900px;
  	}
  }
}