.section-3 {
  position: relative;
  padding-bottom: 73.6%;
  overflow: hidden;
  max-width: 2560px;
  margin: 0 auto;

  &_vid {
    position: absolute;
    display: block;
    bottom: 0;
    top: 0;
    width: 100%;
    z-index: 1;
    left: 50%;
    transform: translateX(-50%);
    width: 131%;
  }

  &_content {
    max-width: 620px;
    margin: 0 auto;
    padding-top: 200px;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
    z-index: 2;
  }

  &_text-box {
    position: relative;
    width: 608px;
    height: 334px;
    margin: 40px auto 40px;
    top: 0;

    &::before {
      content: "";
      position: absolute;
      left: -2px;
      right: -2px;
      top: -3px;
      bottom: -1px;
      background-image: url("../img/text-box-1.png");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center top;
      z-index: 2;
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      border-radius: 7px;
      border: 6px solid rgba(255,255,255,.33);
      left: -5px;
      right: -5px;
      bottom: -4px;
      top: -6px;
      z-index: 1;
    }
  }

  @media screen and (min-width: 1280px) {
    padding-bottom: 67.5%;
    &_vid {
      width: 120%;
    }

    &_text-box {
      margin-top: 140px;
    }
  }

  @media screen and (min-width: 1680px) {

    &_content {
      max-width: 960px;
    }

    &_text-box {
      width: 948px;
      height: 584px;
    }
  }

  @media screen and (min-width: 1920px) {
    &_content {
      padding-top: 280px;
    }
  }

  @media screen and (min-width: 2400px) {
    padding-bottom: 56.2%;
    &_content {
      padding-top: 360px;
    }

    &_vid {
      width: 100%;
    }
  }
}

#v-both,
#v-updated {
  visibility: hidden;
}