.page-footer {

	font-family: "Arial", sans-serif;
	bottom: 0;
	width: 100%;
    z-index: 1230;
    position: relative;

	&_container {
	    max-width: 2560px;
	    margin: 0 auto;
	    display: table;
	    vertical-align: middle;
	    width: 100%;
	    height: 100%;
	}

	&_content {
		position: absolute;
		top: 10%;
		width: 100%;
		text-align: center;
	}

	&_vid {
		width: 100%;
	}

	&_row {
		position: absolute;
		bottom: 0;
	    width: 100%;
		display: flex;
	    flex-direction: row;
	    flex-wrap: nowrap;
	    justify-content: space-around;
	    align-items: flex-end;
		border-top: 1px solid #8ea7ab;
		padding: 20px 10px;
	}

	&_logos {

		img {
			display: inline-block;
			vertical-align: middle;

			&:not(:last-child) {
				margin-right: 40px;
			}
		}
	}

	&_middle {
		text-align: center;
	}

	&_links {
		margin: 0 0 16px;
		padding: 0;
		list-style: none;
		font-size: 12px;

		li {
			display: inline-block;
			vertical-align: middle;
			margin: 0 7.5px;
		}

		a {
			color: #8ea7ab;
			text-decoration: none;

			&:hover {
				text-decoration: underline;
			}
		}
	}

	&_copyrights {
		font-size: 10px;
		text-align: center;
		color: #8ea7ab;
		line-height: 1.4;
	}

	&_rating {
		img {
			display: block;
		}
	}

	@media screen and (min-width: 1280px) {
		&_content {
			padding: 40px 10px;
		}
	}

	@media screen and (min-width: 1680px) {
		&_row {
			padding: 70px 10px;
		}
	}
}