.page-nav {
	position: fixed;
	top: 0;
	bottom: 0;
	width: auto;
	max-width: 2560px;
	font-family: 'ArnoPro', serif;
	z-index: 2000;

	&_menu {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 10px;
		margin: 0;
		padding-left: 36px;
		padding-top: 68px;
		list-style: none;
		height: 324px;

		li {

			&.active {
				.page-nav_menu_item {
					opacity: 1;
				}
			}

			&:not(:last-child) {
				margin-bottom: 13px;
			}
		}

		li:before {
			content: "";
			display: block;
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			width: 17px;
		}

		li:nth-child(1).active::before {
			background-image: url("data:image/png;base64,");
			background-repeat: no-repeat;
		}

		li:nth-child(2).active::before {
			background-image: url("data:image/png;base64,");
			background-repeat: no-repeat;
		}

		li:nth-child(3).active::before {
			background-image: url("data:image/png;base64,");
			background-repeat: no-repeat;
		}

		li:nth-child(4).active::before {
			background-image: url("data:image/png;base64,");
			background-repeat: no-repeat;
		}

		li:nth-child(5).active::before {
			background-image: url("data:image/png;base64,");
			background-repeat: no-repeat;
		}

		&_item {
		    display: inline-block;
    		vertical-align: middle;
			color: #fff;
			text-decoration: none;
			font-size: 20px;
			opacity: .33;
			position: relative;
			height: 16px;
			white-space: nowrap;

			&::before {
				content: "";
				display: none;
				position: absolute;
				left: -14px;
				top: 7px;
				background-color: #FFF;
				width: 5px;
				height: 5px;
				border-radius: 50%;
				opacity: 1;
			}


			&:hover {
				opacity: .66;

				&::before {
					display: block;
				}
			}
		}
	}

	@media screen and (min-width: 1280px) {
		&_menu {

			&_item {
				font-size: 24px;
				height: 20px;

				&::before {
					top: 9px;
				}
			}
		}
	}

	@media screen and (min-width: 1400px) {
		&_menu {
			left: 169px;
		}
	}
}